* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    justify-content: center;
}

.App {
    width: 800px;
}

.post {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid gray;
}

.post-enter {
    opacity: 0;
    transform: translateX(-350px);
}
.post-enter-active {
    opacity: 1;
    transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    transform: translateX(0);
}
.post-exit-active {
    opacity: 0;
    transform: translateX(-350px);
    transition: all 500ms ease-in;
}
.page__wrapper {
    margin: 10px 0;
    display: flex;
    justify-content: center;
}
.page {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 2px;
    border: 1px solid teal;
    cursor: pointer;
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.page:active {
    color: white;
    background: teal;
}

.page__current:active {
    color: white;
    background: orange;
}